@media screen and (max-width: 768px) {
  .block {
    position: relative;
    margin: 3em 0;
  
    &-home {
      .container{
        display: grid;
        grid-template-columns: 1fr;
        gap: 1.5em;
        align-items: center;
      }
  
      img {
        width: 100%;
        max-width: 450px;
        margin: 0 auto;

        grid-row: 2/3;
      }
    }

    &-rafting {
  
      &__description {
  
        &-content {
          &-text {
  
            &-images {
              grid-template-columns: 1fr;
              
              .images-list {
                display: flex;
                flex-wrap: wrap;
                gap: 1em;
                justify-content: center;
                
                img {
                  max-width: 45%;
                }
              }
            }
          }
        }
      }

      &__details {
        img {
          display: none;
        }
      }
    }

    &-jeeping {
  
      &__slider {
  
        &-content {
  
          .slideShow {
            width: 70%;
          }
        }
      }
    }

    &-contact {
    
      &__content {
        grid-template-columns: 1fr;
        gap: 3em;

        &-info {
          grid-row: 1/2;
        }
      }
    }

    &-hotel {
  
      &__location {
        &-content-text {
          display: flex;
          flex-direction: column;
          gap: 2em;
  
          &-destination {
            margin: 0 auto;
            max-width: 450px;
          }
        }
      }
    }
  }
}